import React from 'react';
import avatar from './Avatar.png';

import './chat.scss';

const Chat = ({props}) => {
    
    return (
        <div className='chat'>
            <div className='noll'>
                <span className='noll__name'>Ноль</span> 
                <div className='noll--delite'
                     onClick={props}>
                    <span></span>
                    <span></span>
                </div> 
            </div>
            <div className='assistant'>
                <img className='assistant__img' src={avatar}  alt='assistant'/>
                <div className='assistant__text'>
                    <p>Давай сыграем! Нажми кнопку <span>“Начать игру”</span>.</p>
                </div>
            </div>
        </div>
    )
}

export default Chat;