import React from 'react';
import {Link} from 'react-router-dom';
import logoItem from './crumbs-Button.svg';

import './nav.scss';


const NavMenu = () => {
    return (
        <div className='menu'>
            <Link to={'/'} className='logo'>
                <img className='logo__icon' src={logoItem} alt='logo'/>
            </Link>
            <div className='menu__block'>
                <Link to={'/1'} className='menu__item'>
                    <p>Рождение и развитие арифметики натуральных чисел</p>
                </Link>
                <Link to={'/2'} className='menu__item'>
                    <p>Музей</p>
                </Link>
                <Link to={'/3'} className='menu__item'>
                    <p>Определяем количество без счета</p>
                </Link>
            </div>
        </div>
    )
}

export default NavMenu;