import React, {useState} from 'react';
import cube from './cube.png';
import PointCube from '../point-cube';

import './game.scss';

const Game = () => {

    const [leftSide, setLeftSide] = useState(1);
    const [rightSide, setRightSide] = useState(2);
    const [inputValue, setInputValue] = useState('');
    const[sum, setSum] = useState(leftSide + rightSide);
    
    const randomaizer = () => Math.floor(Math.random() * (7 - 1) + 1);

    const random = () => {
        setLeftSide(randomaizer);
        setRightSide(randomaizer)
    }

    console.log(sum);

    const onValueChangeSum = e => setInputValue(+e.target.value.trim());

    const onSubmit = e => {
        e.preventDefault();
        if (inputValue === '' || inputValue === undefined) {
            alert('Введите число!')
            return
        }
    };

    const winner = () => {
        if (inputValue === sum) {
            console.log('Победа');
        }else {
            console.log('Поражение!');
        }
    }

    return (
        <section className='game'>
            <div className='game__cube'>
                <img src={cube} alt='кубик'/>
                <div className='left__side'>
                    <PointCube/>
                </div>
                <div className='right__side'>
                    <PointCube/>
                </div>
                
            </div>
            <ul className='game__question'>
                <li className='game__item'>Сколько выпало ?</li>
            </ul>
            <form className='form'
                  onSubmit={onSubmit}>
                <label>
                    <input type='number'
                           className='game__input'
                           placeholder='Ввести ответ'
                           onChange={onValueChangeSum}
                           value={inputValue}
                           />
                </label>
                <button onClick={() => {
                    setSum(leftSide + rightSide)
                    winner();
                    random();
                }}
                        className='game__btn'
                        >Отправить
                </button> 
            </form>
        </section>
    )
}

export default Game;