import React from 'react';

import './main-title.scss';

const MainTitle = () => {
    return (
        <div className='title'>
            <h1 className='title__header'>Определяем количество без счета</h1>
            <p className='title__descr'><span className='title__descr--top'>Люди научились считать очень давно.</span> Информация об этом теряется в веках. Был ли период в истории человечества до изобретения цифр, когда еще не было слов для обозначения чисел и символов для их записи?
            <span className='title__descr--bottom'>Можем ли мы определять количество предметов без счёта?</span>
            </p>
        </div>
    )
}

export default MainTitle;