import React, {useState} from 'react';
import NavMenu from '../nav';
import MainTitle from '../main-title';
import Chat from '../chat';
import WorkPanel from '../work-panel';
import './App.scss';

function App() {
    const[closeChat, setClose] = useState(true);

    const toggleVisible = () => {
        setClose((visible) => !visible)
    }
  return (
    <div>
         <NavMenu/>
         <main>
            <div>
                <MainTitle/>
                {closeChat && <Chat props={toggleVisible}/>}
            </div>
            <div >
                <WorkPanel/>
            </div>
         </main>
    </div>
  );
}

export default App;
