import React, {useState} from 'react';
import StartGame from '../start-game';
import Game from '../game';

import './work-panel.scss';

const WorkPanel = () => {
    const [activeGame, setActive] = useState(false)

    const toggleVisible = () => {
        setActive((visible) => !visible)
    }
    return (
        <div className='panel'>
            {activeGame ? <Game/> : <StartGame props={toggleVisible}/>}
            
            <div className='info'>
                <span>[A] - Отправить     [B] - Назад</span>
            </div>
            <div className='control__block'>
                <div className='panel__arrow'>
                    <button className='panel__arrow__control panel__arrow__control--up'></button>
                    <button className='panel__arrow__control panel__arrow__control--down'></button>
                    <button className='panel__arrow__control panel__arrow__control--left'></button>
                    <button className='panel__arrow__control panel__arrow__control--right'></button>
                </div>
                <div className='panel__button'>
                    <button className='panel__button__control'>A</button>
                    <button className='panel__button__control'
                            onClick={toggleVisible}>B</button>
                </div>
            </div>
        </div>
    )
}

export default WorkPanel;